'use client';
import React from 'react';
import Image from 'next/image';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';

const dockItems = [
  { id: 'about-me', title: 'About Me', icon: '/user.svg' },
  { id: 'skills', title: 'Skills', icon: '/code.svg' },
  { id: 'experience', title: 'Experience', icon: '/briefcase.svg' },
  { id: 'projects', title: 'Projects', icon: '/globe.svg' },
  { id: 'education', title: 'Education', icon: '/book.svg' },
  { id: 'trainings', title: 'Trainings', icon: '/award.svg' },
  { id: 'achievements', title: 'Achievements', icon: '/star.svg' },
];

const Dock = () => {
  const scrollToSection = (id: string) => {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <div className="fixed bottom-6 left-1/2 -translate-x-1/2 z-50">
      <div className="flex items-end gap-2 px-4 pb-3 pt-4 bg-white/20 dark:bg-black/20 backdrop-blur-2xl border border-white/30 rounded-3xl shadow-2xl transition-all duration-300 hover:bg-white/30">
        <TooltipProvider delayDuration={100}>
          {dockItems.map((item) => (
            <Tooltip key={item.id}>
              <TooltipTrigger asChild>
                <button
                  onClick={() => scrollToSection(item.id)}
                  className="relative group p-2 rounded-2xl transition-all duration-300 ease-out hover:-translate-y-2 hover:scale-110 focus:outline-none"
                >
                  <div className="relative w-12 h-12 transition-transform duration-300">
                     {/* Add a subtle reflection/glow behind the icon */}
                    <div className="absolute inset-0 bg-white/20 rounded-full blur-md opacity-0 group-hover:opacity-100 transition-opacity" />
                    <Image 
                      src={item.icon} 
                      alt={item.title} 
                      width={48} 
                      height={48} 
                      className="drop-shadow-lg"
                    />
                  </div>
                  {/* Active Dot Indicator (Optional) */}
                  <div className="absolute -bottom-1 left-1/2 -translate-x-1/2 w-1 h-1 bg-black/50 rounded-full opacity-0 group-hover:opacity-100 transition-opacity" />
                </button>
              </TooltipTrigger>
              <TooltipContent side="top" className="bg-black/80 text-white border-0 backdrop-blur-md mb-2">
                <p>{item.title}</p>
              </TooltipContent>
            </Tooltip>
          ))}
        </TooltipProvider>
      </div>
    </div>
  );
};

export default Dock;